import Link from 'next/link';

const Navbar = () => {
    return (
        <nav className="flex justify-between items-center py-4 px-6 bg-orange-500 text-white">
            <div className="text-2xl font-bold">Next 15</div>
            <ul className="flex gap-6">
                <li>
                    <Link href="/" className="hover:text-orange-200 transition">
                        Home
                    </Link>
                </li>
                <li>
                    <Link href="/about" className="hover:text-orange-200 transition">
                        About
                    </Link>
                </li>
                <li>
                    <Link href="/services" className="hover:text-orange-200 transition">
                        Services
                    </Link>
                </li>
                <li>
                    <Link href="/contact" className="hover:text-orange-200 transition">
                        Contact
                    </Link>
                </li>
            </ul>
        </nav>
    );
};

export default Navbar;
